@import 'components/variables';

.section-galeria {
  padding-top: 3rem;
  padding-bottom: 3rem;
  @media (max-width: 424.98px) {
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
  .galeria-ppal {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(6, 1fr);
    grid-template-areas:
      "item1 item1 item2"
      "item1 item1 item2"
      "item1 item1 item2"
      "item3 item4 item2"
      "item3 item4 item5"
      "item3 item4 item5";
    gap: 20px;
    @media (max-width: 991.98px) {
      gap: 15px;
    }
    @media (max-width: 767.98px) {
      gap: 10px;
    }
    @media (max-width: 424.98px) {
      gap: 5px;
    }
    .item1 {
      grid-area: item1;
    }
    .item2 {
      grid-area: item2;
    }
    .item3 {
      grid-area: item3;
    }
    .item4 {
      grid-area: item4;
    }
    .item5 {
      grid-area: item5;
    }
  }
  .ver-mas {
    cursor: pointer;
    color: $blue-claro;
    font-weight: 800;
    text-decoration: underline;
    transition: all .3s;
    margin-top: 1rem;
    &:hover {
      color: $blue-siroco;
    }
    &:focus {
      box-shadow: none;
    }
  }
}

